          id: 0,
          title: 'University of Bristol',
          description: 'Built application for clients, students, and lecturers to interact with their ' +
          'respective projects from the Software Product Engineering unit.',
          tags: [
            {
              id: 1,
              text: 'JS'
            },
            {
              id: 2,
              text: 'Vue.js'
            },
            {
              id: 3,
              text: 'Java'
            },
            {
              id: 4,
              text: 'UX'
            }
          ],
          cover: require('../assets/project-covers/uob-cover.jpg'),
          url: '',
          studyUrl: '/project/bristolSPE'
        },
        {
          id: 1,
          title: 'Alces',
          tags: [
            {
              id: 1,
              text: 'JS'
            },
            {
              id: 2,
              text: 'UX'
            },
            {
              id: 3,
              text: 'Big Data'
            }
