          'Complex data sets generated by monitoring programs can now be ' +
          'summarized, visualized, and analyzed with ease.',
          cover: require('../assets/project-covers/alces-cover.jpg'),
          url: 'https://www.alces.ca/software/',
          studyUrl: '/project/alces'
        },
        {
          id: 2,
          title: 'East European Comic-Con',
          description: 'Was in charge of coordinating the team that handled local and online ' +
          'marketing, design, development, and live online broadcasting.',
          tags: [
            {
              id: 1,
              text: 'Print'
            },
            {
              id: 2,
              text: 'Marketing'
            }
          ],
          cover: require('../assets/project-covers/eecc-cover.jpg'),
          url: 'http://comic-con.ro/',
          studyUrl: '/project/eecc'
        },
        {
          id: 3,
          title: 'Esportsify',
          tags: [
            {
              id: 1,
              text: 'JS'
            },
            {
              id: 2,
              text: 'PHP'
            },
            {
              id: 3,
              text: 'UX'
            }
