<style scoped>
.homepage-project{
  background-size: cover;
  height: 280px;
  position: relative;
}
.homepage-project h1,pre{
  text-align: left;
  color: #fefefe;
  margin: 0px;
  padding: 0px;
}
.homepage-project h1{
  font-size: 2em;
  font-weight: 400;
  text-shadow : 0 3px 4px rgba(0,0,0,0.40);
}
.homepage-project pre{
  font-family: "Work Sans", sans-serif;
  font-weight: 300;
  font-size: 1.1em;
  margin-top: 1.2em;
  width: 600px;
  white-space: pre-wrap
}
.homepage-project .text-container{
  position: absolute;
  top: 50%;
  text-align: center;
  margin-left: 10%;
  -ms-transform: translate(0, -50%);
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
}
.text-container a{
  float: left;
}
@media only screen and (max-width: 780px){
  .homepage-project .text-container pre{
    width: 100%;
    white-space: pre-wrap !important;
    margin-bottom: 0.5em;
  }
  .homepage-project .text-container{
    margin-left: 5%;
    margin-right: 5%;
    text-align: center;
  }
  .text-container a{
    float: none;
  }
}
</style>



